export const navigateTo = (props, navigate) => {
    if(props.location.pathname === '/where-we-are'){
        navigate('/donde-estamos')
      }
      if(props.location.pathname === '/sustainability'){
        navigate('/sostenibilidad')
      }
      if(props.location.pathname === '/contact'){
        navigate('/contacto')
      }
      if(props.location.pathname === '/product/grape-skin-extract'){
        navigate('/producto/extracto-piel-uva')
      }
      if(props.location.pathname === '/product/wine-extract'){
        navigate('/producto/extracto-vino')
      }
      if(props.location.pathname === '/product/grape-tannins'){
        navigate('/producto/taninos-uva')
      }
      if(props.location.pathname === '/product/alcohol'){
        navigate('/producto/alcoholes')
      }
      if(props.location.pathname === '/product/grape-seed'){
        navigate('/producto/pepita-de-uva')
      }
      if(props.location.pathname === '/about-us/resources'){
        navigate('/sobre-nosotros/instalaciones')
      }
      if(props.location.pathname === '/about-us/our-history'){
        navigate('/sobre-nosotros/nuestra-historia')
      }
      if(props.location.pathname === '/about-us/values'){
        navigate('/sobre-nosotros/valores')
      }
}